import React, { Component } from "react";
import FullWidthContainer from "CommonContainers/fullwidthContainer";
import DivColumn from "CommonComponents/divColumn";
import styles from "./signin_page.module.scss";
import InputTextComponent from "CommonComponents/InputTextComponent";
import InputCheckbox from "CommonComponents/InputCheckbox";
import navigatorHoc from "Hoc/navigatorHoc";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { postSigninAction } from "Core/modules/signin/actions/signinActions";
import { CookieService } from "Utils/cookieService";
import { USER_DATA_COOKIE } from "Constants/cookieConstants";

class SignInPage extends Component {
  state = {
    userName: "",
    password: ""
  };

  componentDidMount() {
    const {
      signInReducer: { userDetails },
      navigateTo
    } = this.props;
    if (userDetails) {
      navigateTo("");
    }
  }

  componentWillReceiveProps(nextProps) {
    const {
      signInReducer: { userDetails },
      navigateTo
    } = nextProps;
    if (userDetails) {
      navigateTo("");
    }
  }

  onSubmit = form => {
    form.preventDefault();
    const { postSigninAction, navigateTo } = this.props;
    const { userName, password } = this.state;

    if (userName && password) {
      postSigninAction({
        email: userName, // "buisness@gmail.com",
        password
      }).then(response => {
        const { data, code } = response.payload;
        if (code === 200 || code === 201) {
          CookieService.set(USER_DATA_COOKIE, data);
          navigateTo("");
        }
      });
    }
  };

  render() {
    const { userName, password } = this.state;

    return (
      <FullWidthContainer>
        <DivColumn
          verticalCenter
          horizontalCenter
          className={styles.page_container}
        >
          <div className={styles.signin_title_text}>SignIn</div>
          <form className={styles.form_container} onSubmit={this.onSubmit}>
            <InputTextComponent
              placeholder="Username"
              className={styles.input_text}
              value={userName}
              onChange={event =>
                this.setState({ userName: event.target.value })
              }
            />

            <InputTextComponent
              placeholder="Password"
              className={styles.input_text}
              type="password"
              value={password}
              onChange={event =>
                this.setState({ password: event.target.value })
              }
            />

            <InputCheckbox text="Remember me" />
            <input
              type="submit"
              value="Sign in"
              className={styles.input_submit}
            />
          </form>
          <a className={styles.hyper_link} href="/forgot-password">
            Forgot password
          </a>
          <div className={styles.create_account_container}>
            <span className={styles.new_description_text}>
              New to Maerid?&nbsp;
            </span>
            <a className={styles.hyper_link} href="/signup">
              Create an account
            </a>
          </div>
        </DivColumn>
      </FullWidthContainer>
    );
  }
}

const mapStateToProps = state => {
  return {
    signInReducer: state.signInReducer
  };
};

const mapDispathToProps = dispatch => {
  return {
    postSigninAction: bindActionCreators(postSigninAction, dispatch)
  };
};

export default connect(
  mapStateToProps,
  mapDispathToProps
)(navigatorHoc(SignInPage));
